


.Bars { position: relative; width: 100%; border: 1px solid #B1D632; padding: 0.01rem; height: 1.2rem; margin: 0.1rem 0 0 0;}
.Bars div{ background:#090; height: 100%; line-height: 1.18rem;}
.Bars div span { position: absolute; width: 100%; text-align: center; font-weight: bold; font-size: 10px;}

.pan1{
	margin: 0.2rem 2rem;
}
.pan1 select{
	font-size: 1.2rem;
	margin-right: 2rem;
	vertical-align: middle;
}
.pan2{
	margin: 0.2rem 1rem;
}
.book{
	float: left;
  width: 8rem;
  height: 12rem;
  padding: 0.3rem;
  margin: 0.5rem;
  border: 1px #ccc solid;
  background: #eee;
}
.book .book-title{
  font-size: 1.5rem;
  height: 10.5rem;
}
.book .book-img{
  width:100%;
  height: 10.5rem;
}

.pan-modal{
  position: absolute;
  top:50%;
  left: 50%;
  margin-top: -150px;
  margin-left: -130px;
  height: 300px;
  width: 260px;
  background: #fff;
  border:1px solid #000;
  padding: 20px;
  line-height: 30px;
}



